import { BrowserModule } from '@angular/platform-browser';
import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';

import { MyApp } from './app.component';
import { HomePage } from '../pages/home/home';
import { LoginPage } from "../pages/login/login";
import { TodayPage } from "../pages/today/today";
import { NextweekPage } from "../pages/nextweek/nextweek";
import { TeacherselectPage } from "../pages/teacherselect/teacherselect";
import { TeacherlistPage } from "../pages/teacherlist/teacherlist";
import { NewprojectPage } from "../pages/newproject/newproject";
import { FilterPage } from "../pages/filter/filter";

import { LoginServiceProvider } from '../pages/login/login-service';
import { TodayServiceProvider } from '../pages/today/today-service';
import { NextweekServiceProvider } from '../pages/nextweek/nextweek-service';
import { TeacherselectServiceProvider } from '../pages/teacherselect/teacherselect-service';
import { TeacherlistServiceProvider } from '../pages/teacherlist/teacherlist-service';
import { NewprojectServiceProvider } from '../pages/newproject/newproject-service';
import { FilterServiceProvider } from '../pages/filter/filter-service';

@NgModule({
  declarations: [
    MyApp,
    HomePage,
    LoginPage,
    TodayPage,
    NextweekPage,
    TeacherselectPage,
    TeacherlistPage,
    NewprojectPage,
    FilterPage
  ],
  imports: [
    BrowserModule,
    IonicModule.forRoot(MyApp,{
      mode:'md'
    }, 
{
  links: [
    { loadChildren: '../pages/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/teacherselect/teacherselect.module#TeacherselectPageModule', name: 'TeacherselectPage', segment: 'teacherselect', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/teacherlist/teacherlist.module#TeacherlistPageModule', name: 'TeacherlistPage', segment: 'teacherlist', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/today/today.module#TodayPageModule', name: 'TodayPage', segment: 'today', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/nextweek/nextweek.module#NextweekPageModule', name: 'NextweekPage', segment: 'nextweek', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/newproject/newproject.module#NewprojectPageModule', name: 'NewprojectPage', segment: 'newproject', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/filter/filter.module#FilterPageModule', name: 'FilterPage', segment: 'filter', priority: 'low', defaultHistory: [] }
  ]
})
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp,
    HomePage,
    LoginPage,
    TodayPage,
    NextweekPage,
    TeacherselectPage,
    TeacherlistPage,
    NewprojectPage,
    FilterPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    LoginServiceProvider,
    TodayServiceProvider,
    NextweekServiceProvider,
    TeacherselectServiceProvider,
    TeacherlistServiceProvider,
    NewprojectServiceProvider,
    FilterServiceProvider
  ]
})
export class AppModule {}



